export function navControl() {
  const navbar = document.querySelector('.navbar') as HTMLElement | null;
  const navComponent = document.querySelector('.navbar-component') as HTMLElement | null;
  if (!navbar) return;

  let isFixed = false;

  ScrollTrigger.create({
    trigger: navComponent,
    start: 'bottom top',
    onEnter: () => {
      isFixed = true;
      navbar.classList.add('is-fixed');
    },
    onLeaveBack: () => {
      isFixed = false;
      navbar.classList.remove('is-fixed');
      navbar.classList.remove('is-show');
    },
  });

  setupWebflowLikeScroll(navbar, () => isFixed);
}

function setupWebflowLikeScroll(navbar: HTMLElement, isFixed: () => boolean) {
  let lastScrollY = window.scrollY;

  window.addEventListener(
    'scroll',
    () => {
      if (!isFixed()) return;

      const currentScrollY = window.scrollY;

      if (currentScrollY < lastScrollY) {
        // SCROLL UP
        navbar.classList.add('is-show');
      } else if (currentScrollY > lastScrollY) {
        // SCROLL DOWN
        navbar.classList.remove('is-show');
      }

      lastScrollY = currentScrollY;
    },
    { passive: true }
  );
}

export function testiCustomMouse() {
  const prevTrigger = document.querySelector('.swiper-nav.is-prev');
  const nextTrigger = document.querySelector('.swiper-nav.is-next');
  const prevMouse = document.querySelector('.testi-mouse.is-prev');
  const nextMouse = document.querySelector('.testi-mouse.is-next');

  if (!prevMouse || !nextMouse) return;

  let mouseX = 0;
  let mouseY = 0;

  let x = 0;
  let y = 0;

  let vx = 0; // velocity
  let vy = 0;

  const smoothing = 0.15; // global smoothness
  const friction = 0.8; // momentum decay
  const responsiveness = 0.2; // how fast it chases mouse

  const setPrevX = gsap.quickSetter(prevMouse, 'x', 'px');
  const setPrevY = gsap.quickSetter(prevMouse, 'y', 'px');

  const setNextX = gsap.quickSetter(nextMouse, 'x', 'px');
  const setNextY = gsap.quickSetter(nextMouse, 'y', 'px');

  window.addEventListener('mousemove', (e) => {
    mouseX = e.clientX - window.innerWidth / 2;
    mouseY = e.clientY - window.innerHeight / 2;
  });

  gsap.ticker.add(() => {
    // Hitung difference antara mouse dan cursor
    const dx = mouseX - x;
    const dy = mouseY - y;

    // Responsiveness: seberapa cepat ngikut mouse
    vx += dx * responsiveness;
    vy += dy * responsiveness;

    // Friction: kasih inertia (lebih halus)
    vx *= friction;
    vy *= friction;

    // Update posisi
    x += vx * smoothing;
    y += vy * smoothing;

    setPrevX(x);
    setPrevY(y);

    setNextX(x);
    setNextY(y);
  });

  const prevTl = gsap.timeline({ paused: true });
  prevTl.to(prevMouse, { autoAlpha: 1, scale: 1, duration: 0.3, ease: 'power3.out' });

  const nextTl = gsap.timeline({ paused: true });
  nextTl.to(nextMouse, { autoAlpha: 1, scale: 1, duration: 0.3, ease: 'power3.out' });

  prevTrigger?.addEventListener('mouseenter', () => {
    prevTl.play();
  });

  prevTrigger?.addEventListener('mouseleave', () => {
    prevTl.reverse();
  });
  nextTrigger?.addEventListener('mouseenter', () => {
    nextTl.play();
  });

  nextTrigger?.addEventListener('mouseleave', () => {
    nextTl.reverse();
  });

  // CLICK feedback micro interaction
  const prevClickTl = gsap
    .timeline({ paused: true })
    .to(prevMouse, {
      scale: 0.9,
      duration: 0.12,
      ease: 'power2.out',
    })
    .to(prevMouse, {
      scale: 1,
      duration: 0.12,
      ease: 'power2.in',
    });

  const nextClickTl = gsap
    .timeline({ paused: true })
    .to(nextMouse, {
      scale: 0.9,
      duration: 0.12,
      ease: 'power2.out',
    })
    .to(nextMouse, {
      scale: 1,
      duration: 0.12,
      ease: 'power2.in',
    });

  const clickPrev = () => {
    prevClickTl.restart();
  };

  const clickNext = () => {
    nextClickTl.restart();
  };

  prevTrigger?.addEventListener('click', clickPrev);
  nextTrigger?.addEventListener('click', clickNext);
}

export function brandHover() {
  const brandItems = document.querySelectorAll('.brand-item.is-trigger');
  if (!brandItems.length) return;

  brandItems.forEach((item) => {
    const steps = item.querySelectorAll('.brand-step');
    if (!steps.length) return;

    // INITIAL STATE
    steps.forEach((step, i) => {
      gsap.set(step, {
        yPercent: 100 * (i + 1),
      });
    });

    const tl = gsap.timeline({
      paused: true,
      defaults: {
        ease: 'custom',
        duration: 0.8,
      },
    });

    const totalSteps = steps.length;

    // PHASE-BASED STACK ANIMATION
    for (let phase = 0; phase < totalSteps; phase++) {
      tl.to(
        steps,
        {
          yPercent: (i) => (i - phase) * 100,
        },
        phase * 0.8 // jarak antar phase (feel stack)
      );
    }

    // HOVER IN
    item.addEventListener('mouseenter', () => {
      tl.restart();
    });

    // OPTIONAL: HARD RESET ON OUT
    item.addEventListener('mouseleave', () => {
      tl.pause(0);
      gsap.set(steps, {
        yPercent: (i) => 100 * (i + 1),
      });
    });
  });
}

export function initLoad() {
  const hero = document.querySelector('.hero-wrapper');
  if (!hero) return;

  const head = hero.querySelector('.hero-head');
  const heroReel = hero.querySelector('.heading-reels-wrap');
  const texts = hero.querySelectorAll('.text-18');

  const nav = document.querySelector('.navbar');
  const brand = nav?.querySelector('.brand-logo');
  const navLink = nav?.querySelectorAll('.nav-link');

  const reelsVVideo = document.querySelector('.reels-video-wrap');
  const reelsInner = document.querySelector('.reels-item-inner');

  const splitHead = new SplitText(head, {
    type: 'chars, words',
    wordsClass: 'split-word',
    mask: 'words',
  });

  const splitInner = reelsInner
    ? new SplitText(reelsInner, {
        type: 'chars, words',
        wordsClass: 'split-word',
        mask: 'words',
      })
    : null;

  if (splitInner) {
    gsap.set(splitInner.chars, { yPercent: 100 });
  }

  const splitTexts = texts.length
    ? Array.from(texts).map(
        (text) =>
          new SplitText(text, {
            type: 'lines',
            mask: 'lines',
          })
      )
    : [];

  gsap.set(splitHead.chars, { yPercent: 100 });
  gsap.set(heroReel, { scale: 0 });
  gsap.set(brand, { autoAlpha: 0, y: '2rem' });
  gsap.set(navLink, { autoAlpha: 0, y: '2rem' });
  gsap.set(reelsVVideo, { clipPath: 'inset(0% 0% 0% 100%)' });

  splitTexts.forEach((split) => {
    gsap.set(split.lines, { yPercent: 100 });
  });

  const tl = gsap.timeline();

  tl.to(splitHead.chars, {
    yPercent: 0,
    stagger: {
      amount: 0.2,
    },
    duration: 1.2,
    ease: 'primary-ease',
  })
    .to(
      heroReel,
      {
        scale: 1,
        duration: 1.2,
        ease: 'primary-ease',
      },
      '<0.1'
    )
    .to(
      brand,
      {
        autoAlpha: 1,
        y: '0rem',
        duration: 0.8,
        ease: 'primary-ease',
      },
      '<0.2'
    )
    .to(
      navLink,
      {
        autoAlpha: 1,
        y: '0rem',
        stagger: {
          amount: 0.15,
        },
        duration: 0.8,
        ease: 'primary-ease',
      },
      '<'
    );

  splitTexts.forEach((split) => {
    tl.to(
      split.lines,
      {
        yPercent: 0,
        stagger: {
          amount: 0.01,
        },
        duration: 1.2,
        ease: 'primary-ease',
      },
      '<'
    );
  });

  if (splitInner) {
    tl.to(
      splitInner.chars,
      {
        yPercent: 0,
        stagger: {
          amount: 0.01,
        },
        duration: 1.2,
        ease: 'primary-ease',
      },
      '<'
    ).to(
      reelsVVideo,
      {
        clipPath: 'inset(0% 0% 0% 0%)',
        duration: 1.2,
        ease: 'primary-ease',
      },
      0.1
    );
  }
}

export function initFloatingCTA() {
  const container = document.querySelector('.cta-block');
  if (!container) return;

  const images = gsap.utils.toArray('.cta-img-floating');
  const vw = window.innerWidth;
  const vh = window.innerHeight;

  const cols = 4;
  const rows = 3;
  const zoneW = vw / cols;
  const zoneH = vh / rows;

  // build zones
  const zones = [];
  for (let r = 0; r < rows; r++) {
    for (let c = 0; c < cols; c++) {
      zones.push({ r, c });
    }
  }

  // shuffle zones once
  gsap.utils.shuffle(zones);

  images.forEach((img, i) => {
    // pick zone (cycle if more images than zones)
    const zone = zones[i % zones.length];

    const xMin = zone.c * zoneW;
    const xMax = xMin + zoneW;
    const yMin = zone.r * zoneH;
    const yMax = yMin + zoneH;

    // strong intra zone randomness
    const midX = gsap.utils.random(xMin + zoneW * 0.15, xMax - zoneW * 0.15);
    const midY = gsap.utils.random(yMin + zoneH * 0.15, yMax - zoneH * 0.15);

    // start & end outside viewport
    const startX = gsap.utils.random(-0.5 * vw, 1.5 * vw);
    const startY = gsap.utils.random(-0.5 * vh, 1.5 * vh);
    const endX = gsap.utils.random(-0.5 * vw, 1.5 * vw);
    const endY = gsap.utils.random(-0.5 * vh, 1.5 * vh);

    gsap.set(img, { x: startX, y: startY });

    gsap
      .timeline({
        scrollTrigger: {
          trigger: container,
          start: 'top bottom',
          end: 'bottom top',
          scrub: true,
        },
      })
      .to(img, {
        x: midX,
        y: midY,
        ease: 'none',
        duration: 0.5,
      })
      .to(img, {
        x: endX,
        y: endY,
        ease: 'none',
        duration: 0.5,
      });
  });
}
